<template>
  <pie :title="'游客类型'" :values="values" />
</template>
<script>
import Pie from "@/components/charts/Pie.vue";

export default {
  mounted() {
    this.$getData("/behavior/type").then(list => {
      this.list = list;
      this.$unlock();
    });
  },
  data() {
    return {
      list: []
    };
  },
  computed: {
    values() {
      return this.list.map(({ type, value }) => ({ name: type, value }));
    }
  },
  components: {
    Pie
  }
};
</script>